.close_button {
   mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/close-outline.svg) no-repeat center;
   -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/close-outline.svg) no-repeat center;
   background-color: #fff;
   position: absolute;
   top: 5px;
   right: 5px;
   width: 20px;
   height: 20px;
   cursor: pointer;
   z-index: 999;
}

.graph {
   display: flex;
   width: 100%;
   height: 275px;
}

.info_container {
   width: 100%;
   height: 100%;
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   grid-template-rows: repeat(3, 1fr);
   gap: 15px;
   margin-top: 2px;
   border-radius: 10px;

   :global .info {
      display: flex;
      flex-direction: column;
      border-radius: 10px;
      background-color: #2a2a2a;
      padding: 10px;

      :global .label {
         font-size: 12px;
         color: #9f9f9f;
      }

      :global .value {
         font-size: 36px;
         text-align: center;
      }

      :global .extra {
         font-size: 14px;
         text-align: center;
      }
   }
}