.graph {
   display: flex;
   width: 100%;
   height: 275px;
}

.list_header {
   display: grid;
   grid-template-columns: 1fr 1fr 1fr 1fr 50px;
   width: 100%;
   padding: 0px 10px 0px 10px;
   margin-top: 10px;

   :global span:not(:first-child):not(:last-child) {
      text-align: center;
   }

   :global span:last-child {
      text-align: right;
   }
}

.list_container {
   display: flex;
   flex-direction: column;
   width: 100%;
   flex: 1;
   overflow-y: scroll;
   margin-top: 2px;
   border-radius: 10px;

   :global .entry {
      background-color: #2a2a2a;
      margin-bottom: 5px;
      border-radius: 10px;
      padding: 10px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 50px;
      cursor: pointer;

      &:hover {
         background-color: #272727;
      }

      &:active {
         background-color: #252525;
      }

      :global span:not(:first-child):not(:last-child) {
         text-align: center;
      }

      :global span:last-child {
         text-align: right;
      }
   }
}