* {
   box-sizing: border-box;
}

body {
   background-color: #1a1a1a;
   color: #f1f1f1;
   font-family: Roboto;
   height: 100%
}

html {
   height: 100%;
   scrollbar-face-color: #646464;
   scrollbar-base-color: #646464;
   scrollbar-3dlight-color: #646464;
   scrollbar-highlight-color: #646464;
   scrollbar-track-color: #000;
   scrollbar-arrow-color: #000;
   scrollbar-shadow-color: #646464;
}

#root {
   height: 100%;
}

.outer_container {
   width: 100%;
   height: 100%;
   display: grid;
   grid-template-columns: 80px 1fr;
   grid-template-areas: "menu content";

   :global .menu {
      display: flex;
      flex-direction: column;
      justify-content: center;
      grid-area: menu;
      background-color: #1f1f1f;
      align-items: center;

      :global .menuitem {
         width: 50px;
         height: 50px;
         background-color: #ddd;
         margin: 10px 0px;
         cursor: pointer;
      }

      :global .wallet {
         mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/wallet-outline.svg) no-repeat center;
         -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/wallet-outline.svg) no-repeat center;
      }
      :global .wallet.active {
         mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/wallet.svg) no-repeat center;
         -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/wallet.svg) no-repeat center;
      }

      :global .stats {
         mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/stats-chart-outline.svg) no-repeat center;
         -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/stats-chart-outline.svg) no-repeat center;
      }
      :global .stats.active {
         mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/stats-chart.svg) no-repeat center;
         -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/stats-chart.svg) no-repeat center;
      }
   }

   .page_container {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 50px;

      :global .header {
         grid-area: header;
         font-size: 36px;
         font-weight: bold;
         margin-left: 10px;
      }

      :global .instance {
         grid-area: instance;
         display: flex;
         flex-direction: row;
         align-items: center;
         margin-left: 10px;
         margin-bottom: 8px;

         :global .icon {
            width: 25px;
            height: 25px;
            mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/paper-plane.svg) no-repeat center;
            -webkit-mask: url(https://unpkg.com/ionicons@5.4.0/dist/svg/paper-plane.svg) no-repeat center;
            background-color: #42c2f4; //#ef9c37;
            margin-right: 5px;
         }

         :global .instance_select {
            :global .Dropdown-control {
               background-color: transparent;
               color: inherit;
               border: none;
               height: 20px;
               padding: 0px 20px 0px 0px;
               cursor: pointer;

               :global .Dropdown-arrow {
                  top: 50%;
                  right: 5px;
                  transform: translateY(-50%);
               }
            }

            :global .Dropdown-menu {
               border: 1px solid #2a2a2a;
               width: max-content;
               :global .Dropdown-option {
                  background-color: #1f1f1f;
                  color: inherit;

                  &:last-child {
                     border-radius: 0px;
                  }

                  &:hover {
                     background-color: #252525;
                  }
               }
            }
         }
      }
   }
}

::-webkit-scrollbar {
   display: none;
}