.pie_container {
   position: relative;
   width: 250px;
   height: 250px;
   margin-top: 30px;
   margin-bottom: 20px;

   :global .center_info_container {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 80%;
      height: 80%;
      border-radius: 50%;
      background-color: #2a2a2a;

      :global .secondary {
         color: #9f9f9f;
      }
   }
}

.list_container {
   display: flex;
   flex-direction: column;
   width: 100%;
   height: 100%;
   overflow-y: scroll;
   border-radius: 10px;

   :global .entry {
      width: "100%";
      background-color: #2a2a2a;
      margin-bottom: 5px;
      border-radius: 10px;
      display: flex;
      flex-direction: row;
      padding: 10px;
      align-items: center;

      :global .color {
         width: 20px;
         height: 20px;
         border-radius: 50%;
         margin-right: 5px;
      }
   }
}